<template>
  <div class="wrapper" ref="wrapper">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BS from 'better-scroll'

  export default {
    name: "scroll",
    data(){
      return{
        scrolls: null
      }
    },
    props:{
      probeType:{
        type:Number,
        default:0
      },
      pullUpLoad:{
        type:Boolean,
        default:false
      }
    },
    mounted() {
      // scrolls  queryselectdom
      this.scrolls = new BS(this.$refs.wrapper,{
        // truediv
        click:true,
        // 
        probeType:this.probeType,
        // 
        pullUpLoad:this.pullUpLoad
      })

      // 
      if (this.probeType == 2 || this.probeType == 3){
        this.scrolls.on('scroll',(position)=>{
          this.$emit('scroll',position)
        })
      }

      // 
      if (this.pullUpLoad){
        this.scrolls.on('pullingUp',()=>{
          this.$emit('pullingUp')
        })
      }
    },
    methods:{
      // 
      scrollTos(x,y,time=1000){
        this.scrolls && this.scrolls.scrollTo(x, y, time)
      },

      // 
      finishPullUp(){
       this.scrolls.finishPullUp()
      },

      // scrollscrollheight
      refresh(){
        // 
        // console.log('------');
        this.scrolls && this.scrolls.refresh()
      },
      // y
      getScrollY(){
        return this.scrolls ? this.scrolls.y :0
      }
    }
  }
</script>

